.specific-container {
    background: #b0b0b0;
    background-image: url("../../Images/images-pokemon.jpg");
    background-size: cover;
    background-blend-mode:soft-light;
    min-height: 100vh;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.specific-wrapper {
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.text-container {
    margin-top: 1em;
}

.specific-wrapper h1 {
    text-transform: capitalize;
    margin-bottom: .5em;
}

.main-img img {
    display: block;
    height: 250px;
    width: 250px;
    margin: 0 auto;
}

.pokemon-img-secondary {
    width: 30%;
    display: flex;
    justify-content: center;
    margin: 0 auto 2em;
}

.data-cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 1em;
    background: rgba(255,255,255,.5);
    border-radius: 12px;
    padding: 1em;
    text-transform: capitalize;
}

.height {
    flex-direction: row;
}

h3 + h3 {
    margin-left: 1em;
}

.stats h3,
.moves h3 {
    margin: 0 auto .5em;
    text-align: center;
}

