.pagination-wrapper {
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.page-btn-container {

}

.btn {
    display:inline-block;
    padding: 1em 3em;
    background: transparent;
    color: #fff;
    font-weight: 700;
    cursor: pointer;
    border: 2px solid #fff;
    
    &:hover,
    &:focus {
        border: 2px solid magenta;
    }
}

:disabled {
    background: rgb(218, 69, 69);
    cursor: not-allowed;
    opacity: .8;

    &:hover {
        border: 2px solid #fff;
    }
}