*,
*::before,
*::after {
    margin: 0;
    padding: 0; 
    box-sizing: border-box;
}

body {
    font-family: 'Oswald', sans-serif;
    line-height: 1.2;
    letter-spacing: 1.2px;
}

h1 { font-size: 2.2rem; margin: .67em 0 }
h2 { font-size: 1.5rem; margin: .75em 0 }
h3 { font-size: 1.1rem; margin: .83em 0 }


@media (min-width: 720px) {
    h1 {font-size: 2.5rem}
    h2 {font-size: 1.8rem}
    h3 {font-size: 1.3rem;}
    
}

@media (min-width: 1040px) {
    h1 {font-size: 2.8rem}
    h2 {font-size: 2rem}
    h3 {font-size: 1.5rem;}
}



